import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false

//引入elementUI
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);

//引入reset.css
import 'reset-css';

//引入axios的index.js
import './axios/index';

//加载resize.js 监听窗口的
import './assets/resize';

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
